import React from 'react'
import { Row, Col, Card, Avatar, Button, message, Popconfirm } from 'antd';
import { Icon } from 'components/util-components/Icon';
import { useHistory } from 'react-router-dom';
import {
    GlobalOutlined, MailOutlined,
    DollarCircleOutlined, HomeOutlined,
    PlusCircleOutlined
} from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';

import PageHeaderAlt from 'components/layout-components/PageHeaderAlt';
import Flex from 'components/shared-components/Flex';

import { campaign_banner, campaign_logo } from 'api';
import AddUserForm from './AddUserForm';

const CampaignDetails = (props) => {
    let { data, user_id } = props;
    let history = useHistory();
    let [formVisible, setFormVisible] = React.useState(false);

    const editPress = async () => {
        try {
            history.push('/campaign/form', data);
        } catch (error) {
            console.log(error);
        }
    }
    const deletePress = async () => {
        let res = await axios.post('/deletecampaign', { user_id, campaign_id: data.campaign_id });
        console.log(res.data);
        message.success(res.data.message)
        history.goBack();
    }

    return (
        <>
            <AddUserForm
                formVisible={formVisible}
                setFormVisible={setFormVisible}
                campaign_id={data.campaign_id}
            />
            <PageHeaderAlt background={campaign_banner + data.campaign_banner} cssClass="bg-primary" overlap>
                <div className="container text-center">
                    <div className="py-5 my-md-5">
                    </div>
                </div>
            </PageHeaderAlt>
            <Card>
                <div className="container my-4">
                    <Row justify="center">
                        <Col sm={24} md={23}>
                            <div className="d-md-flex">
                                <div className="rounded p-2 bg-white shadow-sm mx-auto" style={{ 'marginTop': '-3.5rem', 'maxWidth': `${150 + 16}px` }}>
                                    <Avatar shape="square" size={150} src={campaign_logo + data.campaign_logo} />
                                </div>
                                <div className="ml-md-4 w-100">
                                    <Flex alignItems="center" mobileFlex={false} className="mb-3 text-md-left text-center">
                                        <h2 className="mb-0">{data.campaign_campaignname}</h2>
                                        <div className="ml-md-3 mt-3 mt-md-0" style={{ flex: 1 }}>
                                            <Button size="small" type="primary" onClick={editPress}>Edit</Button>
                                            <Popconfirm
                                                placement="topRight"
                                                title={'Are you sure, you want to delete this campaign? '}
                                                onConfirm={deletePress}
                                                okText="Yes"
                                                cancelText="No"
                                            >
                                                <Button size="small" className="ml-2">Delete</Button>
                                            </Popconfirm>
                                            <Button
                                                size="small"
                                                type="primary"
                                                style={{ float: 'right', display: 'flex', alignItems: 'center' }}
                                                className="ml-2"
                                                icon={<PlusCircleOutlined style={{ fontSize: 18, marginTop: 1 }} />}
                                                onClick={() => setFormVisible(true)}
                                            >
                                                Add User
                                            </Button>
                                        </div>
                                    </Flex>
                                    <Row gutter="16">
                                        <Col sm={24} md={8}>
                                            <p className="mt-0 mr-3 text-muted text-md-left text-center">
                                                {data.campaign_aboutus}
                                            </p>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Row className="mb-2">
                                                <Col xs={12} sm={12} md={9}>
                                                    <Icon type={MailOutlined} className="text-primary font-size-md" />
                                                    <span className="text-muted ml-2">Email:</span>
                                                </Col>
                                                <Col xs={12} sm={12} md={15}>
                                                    <span className="font-weight-semibold">{data.campaign_email}</span>
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col xs={12} sm={12} md={9}>
                                                    <Icon type={HomeOutlined} className="text-primary font-size-md" />
                                                    <span className="text-muted ml-2">Address:</span>
                                                </Col>
                                                <Col xs={12} sm={12} md={15}>
                                                    <span className="font-weight-semibold">{data.location_name}</span>
                                                </Col>
                                            </Row>
                                        </Col>
                                        <Col xs={24} sm={24} md={8}>
                                            <Row className="mb-2 mt-2 mt-md-0 ">
                                                <Col xs={12} sm={12} md={9}>
                                                    <Icon type={DollarCircleOutlined} className="text-primary font-size-md" />
                                                    <span className="text-muted ml-2">Currency:</span>
                                                </Col>
                                                <Col xs={12} sm={12} md={15}>
                                                    <span className="font-weight-semibold">{data.currency_name}</span>
                                                </Col>
                                            </Row>
                                            <Row className="mb-2">
                                                <Col xs={12} sm={12} md={9}>
                                                    <Icon type={GlobalOutlined} className="text-primary font-size-md" />
                                                    <span className="text-muted ml-2">Website:</span>
                                                </Col>
                                                <Col xs={12} sm={12} md={15}>
                                                    <span className="font-weight-semibold">{data.campaign_website}</span>
                                                </Col>
                                            </Row>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </div>
            </Card>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(CampaignDetails);