import React from 'react';
import { Row, Col, Avatar, Button, Modal, Form, Select, Input, message } from 'antd';
import { UserOutlined, UploadOutlined } from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';

import Flex from 'components/shared-components/Flex';


let { Option } = Select;

const UserForm = (props) => {
    let { formVisible, setFormVisible, user_id, campaign_id, } = props;
    let [roles, setRoles] = React.useState([]);
    let [state, setState] = React.useState({
        imagePreview: '',
        image: ''
    })

    const getRoles = async () => {
        try {
            let res = await axios.post('/role', { user_id });
            setRoles(res.data.data);
        } catch (error) {
            console.log(error);
        }
    }

    const addUser = async (values) => {
        let temp = message.loading('Creating User', 0);
        try {
            let formData = new FormData();
            formData.append('name', values.name);
            formData.append('email', values.email);
            formData.append('officenumberext', values.officenumberext);
            formData.append('phonenumber', values.phonenumber);
            formData.append('username', values.username);
            formData.append('target', values.target);
            formData.append('password', values.password);
            formData.append('confirmpassword', values.confirmpassword);
            formData.append('picture', state.image);
            formData.append('role_id', values.role_id);
            formData.append('campaign_id', campaign_id);
            formData.append('user_id', user_id);
            await axios.post('/createuser', formData);
            temp()
            message.success('User Created');
            setFormVisible(false);
        } catch (error) {
            temp()
            message.error(error.response.data);
            console.log(error);
        }
    }

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    const openFile = () => {
        let btn = document.createElement('input');
        btn.type = 'file';
        btn.onchange = e => {
            let file = e.target.files[0];
            setState({ ...state, image: file, imagePreview: URL.createObjectURL(file) })
        }
        btn.click();
    }

    React.useEffect(() => {
        getRoles();
    }, []);

    return (
        <Modal
            title="Add User"
            centered
            visible={formVisible}
            onOk={() => setFormVisible(false)}
            onCancel={() => setFormVisible(false)}
            width={900}
            footer={null}
            okText='Add User'
        >
            <Flex alignItems="center" justifyContent='center' mobileFlex={true} className="text-center text-md-left">
                <Avatar size={90} icon={<UserOutlined />} src={state.imagePreview} />
                <div className="ml-md-3 mt-md-0 mt-3">
                    <Button type="primary" icon={<UploadOutlined />} onClick={openFile}>Profile Picture</Button>
                </div>
            </Flex>
            <div className="mt-4">
                <Form
                    name="basicInformation"
                    layout="verticle    "
                    onFinish={addUser}
                    onFinishFailed={onFinishFailed}
                >
                    <Row style={{ justifyContent: 'center' }}>
                        <Col xs={24} sm={24} md={24} lg={20} >
                            <Row gutter={16}>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Name"
                                        name="name"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please input Campaign Name!',
                                            },
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Role"
                                        name="role_id"
                                        rules={[
                                            {
                                                required: true,
                                                message: 'Please Select Currency!'
                                            },
                                        ]}
                                    >
                                        <Select>
                                            {
                                                roles.map(item => {
                                                    return (
                                                        <Option key={item.role_id} value={item.role_id}>
                                                            {item.role_name}
                                                        </Option>
                                                    )
                                                })
                                            }
                                        </Select>
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Email"
                                        name="email"
                                        rules={[{
                                            required: true,
                                            type: 'email',
                                            message: 'Please enter a valid email!'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Office Number"
                                        name="officenumberext"
                                        rules={[{
                                            required: true,
                                            message: 'Required!'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Phone Number"
                                        name="phonenumber"
                                        rules={[{
                                            required: true,
                                            message: 'Required!'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Username"
                                        name="username"
                                        rules={[{
                                            required: true,
                                            message: 'Enter a valid website'
                                        }]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Target"
                                        name="target"
                                        rules={[{
                                            required: true,
                                            message: 'Required',
                                        }
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Password"
                                        name="password"
                                        rules={[{
                                            required: true,
                                            message: 'Required',

                                        }
                                        ]}
                                    >
                                        <Input />
                                    </Form.Item>
                                </Col>
                                <Col xs={24} sm={24} md={12}>
                                    <Form.Item
                                        label="Confirm Password"
                                        name="confirmpassword"
                                        rules={[{
                                            required: true,
                                            message: 'Required',
                                        }
                                        ]}
                                    >
                                        <Input type="password" />
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Button type="primary" htmlType="submit" style={{ float: 'right' }}>
                                Add User
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </div>
        </Modal>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps, {})(UserForm);