import React, { Component } from 'react'
import { Card, Table, Tag, Tooltip, message, Button, Col, Row, Popconfirm } from 'antd';
import { EyeOutlined, DeleteOutlined } from '@ant-design/icons';
import axios from 'axios';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import { user_image } from 'api';

import EditUserForm from './EditUserForm';

const UsersList = (props) => {
    let { role, data, user_id } = props;
    let [formVisible, setFormVisible] = React.useState(false);
    let [editUserId, setEditUserId] = React.useState('');

    const editBtnClick = (id) => {
        setEditUserId(id);
        setFormVisible(true);
    }

    const deleteBtnClick = async (edituser_id) => {
        try {
            await axios.post('/deleteuser', { user_id, edituser_id });
            message.success('User Deleted');
        } catch (error) {
            console.log(error);
        }
    }

    const agentColumn = [
        {
            title: role,
            dataIndex: 'name',
            render: (_, record) => (
                <div className="d-flex">
                    <AvatarStatus src={user_image + record.user_picture} name={record.user_name} subTitle={record.user_email} />
                </div>
            ),
            sorter: (a, b) => {
                a = a.user_name.toLowerCase();
                b = b.user_name.toLowerCase();
                return a > b ? -1 : b > a ? 1 : 0;
            },
            filterMultiple: true,
            onFilter: (value, record) => record.name.indexOf(value) === 0,
        },
        {
            title: 'Phone Number',
            dataIndex: 'phone',
            render: (_, record) => (
                <div className="d-flex">
                    {record.user_phonenumber}
                </div>
            ),
        },
        {
            title: 'Target',
            dataIndex: 'role',
            render: (_, record) => (
                <div className="d-flex">
                    {record.user_target}
                </div>
            ),
        },
        {
            title: 'Login Status',
            dataIndex: 'status',
            render: (_, record) => (
                <Tag className="text-capitalize" color={record.user_loginstatus === 'Online' ? 'cyan' : 'red'}>{record.user_loginstatus}</Tag>
            ),
            sorter: (a, b) => a.user_loginstatus.length - b.user_loginstatus.length,
        },
        {
            title: '',
            dataIndex: 'actions',
            render: (_, record) => (
                <div className="text-right">
                    <Tooltip title="Edit">
                        <Button
                            type="primary"
                            className="mr-2"
                            icon={<EyeOutlined />}
                            size="small"
                            onClick={() => editBtnClick(record.user_id)}
                        />
                    </Tooltip>
                    <Popconfirm
                        placement="topRight"
                        title={'Are you sure, you want to delete this user? '}
                        onConfirm={() => deleteBtnClick(record.user_id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Tooltip title="Remove">
                            <Button danger icon={<DeleteOutlined />} size="small" />
                        </Tooltip>
                    </Popconfirm>
                </div>
            )
        }
    ];

    const designerColumn = [
        {
            title: role,
            dataIndex: 'name',
            render: (_, record) => (
                <div className="d-flex">
                    <AvatarStatus src={user_image + record.user_picture} name={record.user_name} subTitle={record.user_email} />
                </div>
            ),
            sorter: (a, b) => {
                a = a.user_name.toLowerCase();
                b = b.user_name.toLowerCase();
                return a > b ? -1 : b > a ? 1 : 0;
            },
            filterMultiple: true,
            onFilter: (value, record) => record.name.indexOf(value) === 0,
        },
        {
            title: 'Phone Number',
            dataIndex: 'phone',
            render: (_, record) => (
                <div className="d-flex">
                    {record.user_phonenumber}
                </div>
            ),
        },
        {
            title: 'Login Status',
            dataIndex: 'status',
            render: (_, record) => (
                <Tag className="text-capitalize" color={record.user_loginstatus === 'Online' ? 'cyan' : 'red'}>{record.user_loginstatus}</Tag>
            ),
            sorter: (a, b) => a.user_loginstatus.length - b.user_loginstatus.length,
        },
        {
            title: '',
            dataIndex: 'actions',
            render: (_, record) => (
                <div className="text-right">
                    <Tooltip title="Edit">
                        <Button
                            type="primary"
                            className="mr-2"
                            icon={<EyeOutlined />}
                            size="small"
                            onClick={() => editBtnClick(record.user_id)}
                        />
                    </Tooltip>
                    <Popconfirm
                        placement="topRight"
                        title={'Are you sure, you want to delete this? '}
                        onConfirm={() => deleteBtnClick(record.user_id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Tooltip title="Remove">
                            <Button
                                danger
                                icon={<DeleteOutlined />}
                                size="small"
                            />
                        </Tooltip>
                    </Popconfirm>
                </div>
            )
        }
    ];

    return (
        <Row style={{ justifyContent: 'center' }}>
            <EditUserForm
                formVisible={formVisible}
                setFormVisible={setFormVisible}
                campaign_id={props.campaign_id}
                edituser_id={editUserId}
            />
            <Col lg={22} style={{ alignSelf: 'center' }}>
                <Card bodyStyle={{ 'padding': '0px' }}>
                    <Table
                        columns={
                            (role === 'Managers' || role === 'Agents') ?
                                agentColumn
                                :
                                designerColumn
                        }
                        dataSource={data}
                        rowKey='user_id'
                    />
                </Card>
            </Col>
        </Row>
    )
}

export default UsersList;