import React from 'react';
import { Row, Col, Avatar, Button, Modal, Form, Select, Input, message } from 'antd';
import { UserOutlined, UploadOutlined } from '@ant-design/icons';
import axios from 'axios';
import { connect } from 'react-redux';

import Flex from 'components/shared-components/Flex';


let { Option } = Select;

const EditUserForm = (props) => {
    let { formVisible, setFormVisible, user_id, campaign_id, edituser_id } = props;
    let [roles, setRoles] = React.useState([]);
    let [user, setUser] = React.useState(null);
    let [state, setState] = React.useState({
        imagePreview: '',
        image: ''
    })

    const getRoles = async () => {
        try {
            let res = await axios.post('/role', { user_id, });
            setRoles(res.data.data);
        } catch (error) {
            console.log(error);
        }
    }

    const updateUser = async (values) => {
        let temp = message.loading('Updating User', 0);
        try {
            let formData = new FormData();
            formData.append('edituser_id', edituser_id);
            formData.append('name', values.name);
            formData.append('email', values.email);
            formData.append('officenumberext', values.officenumberext);
            formData.append('phonenumber', values.phonenumber);
            formData.append('username', values.username);
            formData.append('target', values.target);
            formData.append('role_id', values.role_id);
            formData.append('campaign_id', campaign_id);
            formData.append('user_id', user_id);
            if (state.image) {
                formData.append('picture', state.image);
            }
            if (values.password) {
                formData.append('password', values.password);
            }

            await axios.post('/updateuser', formData);

            temp();
            message.success('User Updated');
            setFormVisible(false);
        } catch (error) {
            temp()
            message.error(error.response.data);
            console.log(error);
        }
    }

    const onFinishFailed = errorInfo => {
        console.log('Failed:', errorInfo);
    };

    const openFile = () => {
        let btn = document.createElement('input');
        btn.type = 'file';
        btn.onchange = e => {
            let file = e.target.files[0];
            setState({ ...state, image: file, imagePreview: URL.createObjectURL(file) })
        }
        btn.click();
    }

    const getUser = async () => {
        try {
            let res = await axios.post('/userdetails', { user_id, edituser_id });
            setUser(res.data.data);
        } catch (error) {
            console.log(error.response.data);
        }
    }

    React.useEffect(() => {
        getRoles();
    }, []);
    React.useEffect(() => {
        if (user_id && edituser_id) {
            getUser();
        }
    }, [edituser_id]);
    return (
        <Modal
            title="Add User"
            centered
            visible={formVisible}
            onOk={() => setFormVisible(false)}
            onCancel={() => setFormVisible(false)}
            width={900}
            afterClose={() => setUser(null)}
            footer={null}
            okText='Add User'
        >
            <Flex alignItems="center" justifyContent='center' mobileFlex={true} className="text-center text-md-left">
                <Avatar size={90} icon={<UserOutlined />} src={state.imagePreview} />
                <div className="ml-md-3 mt-md-0 mt-3">
                    <Button type="primary" icon={<UploadOutlined />} onClick={openFile}>Profile Picture</Button>
                </div>
            </Flex>
            {
                user &&
                <div className="mt-4">
                    <Form
                        name="basicInformation"
                        layout="verticle    "
                        onFinish={updateUser}
                        onFinishFailed={onFinishFailed}
                    >
                        <Row style={{ justifyContent: 'center' }}>
                            <Col xs={24} sm={24} md={24} lg={20} >
                                <Row gutter={16}>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Name"
                                            name="name"
                                            initialValue={user.user_name}
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Please input Name!',
                                                },
                                            ]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Role"
                                            name="role_id"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Please Select Role!'
                                                },
                                            ]}
                                            initialValue={user.role_id}
                                        >
                                            <Select>
                                                {
                                                    roles.map(item => {
                                                        return (
                                                            <Option key={item.role_id} value={item.role_id}>
                                                                {item.role_name}
                                                            </Option>
                                                        )
                                                    })
                                                }
                                            </Select>
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Email"
                                            name="email"
                                            initialValue={user.user_email}
                                            rules={[{
                                                required: true,
                                                type: 'email',
                                                message: 'Please enter a valid email!'
                                            }]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Office Number"
                                            name="officenumberext"
                                            initialValue={user.user_officenumberext}
                                            rules={[{
                                                required: true,
                                                message: 'Required!'
                                            }]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Phone Number"
                                            name="phonenumber"
                                            initialValue={user.user_officenumberext}
                                            initialValue={user.user_officenumberext}
                                            rules={[{
                                                required: true,
                                                message: 'Required!'
                                            }]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Username"
                                            name="username"
                                            initialValue={user.user_username}
                                            rules={[{
                                                required: true,
                                                message: 'required'
                                            }]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Target"
                                            name="target"
                                            initialValue={user.user_target}
                                            rules={[{
                                                required: true,
                                                message: 'Required',
                                            }
                                            ]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={24} md={12}>
                                        <Form.Item
                                            label="Password"
                                            name="password"
                                            rules={[{
                                                message: 'Required',
                                            }
                                            ]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                </Row>
                                <Button type="primary" htmlType="submit" style={{ float: 'right' }}>
                                    Update User
                            </Button>
                            </Col>
                        </Row>
                    </Form>
                </div>

            }
        </Modal>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(EditUserForm);