import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { message } from 'antd';

import ProfileHeader from 'components/campaign/ProfileHeader';
import UsersList from 'components/campaign/UsersList';
import Loading from 'components/shared-components/Loading';

const CampaignDetails = (props) => {
    let { match, user_id } = props;
    let campaign_id = match.params.campaign_id;

    let [loading, setLoading] = React.useState(true);
    let [data, setData] = React.useState({});
    let [userList, setUserList] = React.useState({})

    const getCampaignDetails = async () => {
        try {
            let res = await axios.post('/campaigndetails', { user_id, campaign_id });
            setData(res.data.data);
            setLoading(false);
            message.success(res.data.message);
        } catch (error) {
            console.log(error)
            setLoading(false);
            message.error(error.response.data);
        }
    }

    const getUsers = async () => {
        try {
            let res = await axios.post('/userlist', { user_id, campaign_id });
            setUserList(res.data);
        } catch (error) {
            console.log(error.message)
            message.error(error.response.data);
        }
    }

    React.useEffect(() => {
        getCampaignDetails();
        getUsers();
    }, []);

    if (loading) {
        return <Loading cover='content' />
    }
    return (
        <>
            <ProfileHeader
                data={data}
            />
            <UsersList
                role='Managers'
                showTarget={true}
                data={userList.manager}
                user_id={user_id}
            />
            <UsersList
                role='Agents'
                showTarget={true}
                data={userList.agent}
                campaign_id={data.campaign_id}
                user_id={user_id}
            />
            <UsersList
                role='Designers'
                showTarget={false}
                data={userList.designer}
                campaign_id={data.campaign_id}
                user_id={user_id}
            />
            <UsersList
                role='Digitizers'
                showTarget={false}
                data={userList.digitizer}
                campaign_id={data.campaign_id}
                user_id={user_id}
            />
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id
});

export default connect(mapStateToProps)(CampaignDetails);